import $http from '@/common/api/request.js'
import Card from '@/components/common/Card.vue';
import CommodityList from '@/components/common/CommodityList.vue';
import UniNumberBox from '@/components/uni/uni-number-box/uni-number-box.vue';
import {mapMutations} from 'vuex'
export default {
	data() {
		return {
			isShow: false,
			num:1,
			MinNum:1,
			animationData: {},
			goodsContent:{},
			swiperList: [{
					imgurl: "../../static/img/details1.jpeg"
				},
				{
					imgurl: "../../static/img/details2.jpeg"
				},
				{
					imgurl: "../../static/img/details3.jpeg"
				}
			],
			dataList: [{
					id: 1,
					imgUrl: "../../static/img/commodity1.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				},
				{
					id: 2,
					imgUrl: "../../static/img/commodity2.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				},
				{
					id: 3,
					imgUrl: "../../static/img/commodity3.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				},
				{
					id: 4,
					imgUrl: "../../static/img/commodity4.jpg",
					name: "大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice: "299",
					oprice: "659",
					discount: "5.2"
				}
			]
		}
	},
	components: {
		Card,
		CommodityList,
		UniNumberBox
	},
	onNavigationBarButtonTap(e) {
		if(e.type==='share'){
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 0,
			    title:this.goodsContent.name,
				summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
			    href:"http://192.168.1.101:8080/#/pages/details/details?id="+this.goodsContent.id,
				imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
				success: function (res) {
			      uni.showTabBar({
			      	title:'分享成功'
			      })
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			});
		}
	},
	//监听页面返回 
	onBackPress() {
		if(this.isShow){
			this.hidePop();
			return true; 
		}
	},
	onLoad(e) {
		this.getData(e.id)
	},
	methods: {
		...mapMutations(['addShopCart']),
		//请求数据
		getData(id){
			$http.request({
				url:"/goods/id",
				data:{
					id
				}
			}).then((res)=>{
				this.goodsContent=res[0]
			}).catch(()=>{
				uni.showToast({
					title:'请求失败',
					icon:'none'
				})
			})
		},
		showPop() {
			var animation = uni.createAnimation({
				duration: 200
			})
			animation.translateY(600).step();
			this.animationData = animation.export();
			this.isShow = true;
			setTimeout(() => {
				animation.translateY(0).step();
				this.animationData = animation.export();
			}, 200)
		},
		hidePop() {
			var animation = uni.createAnimation({
				duration: 200
			})
			animation.translateY(600).step();
			this.animationData = animation.export();
			this.isShow = true;
			setTimeout(() => {
				animation.translateY(0).step();
				this.isShow = false;
			}, 200)

		},
		changeNumber(val){
			this.num=val;
		},
		//点击确定
		addCart(){
			let goods=this.goodsContent;
			this.goodsContent['checked']=false;
			this.goodsContent['num']=this.num;
			
			
			this.addShopCart(goods);
			this.hidePop();
			
			uni.showToast({
				title:"加入购物车成功",
				icon:"none"
				
			})
		},
		//跳转购物车
		goShopCart(){
			uni.navigateTo({
				url:'../shopcart/shopcart'
			})
		}
	}
}
